import { defineComponent, ref } from 'vue';
import prettier from 'prettier';
import babelParser from 'prettier/parser-babel';
import { Button, Select, Option, Input, Message } from 'element-ui';
import qs from 'qs';
export default defineComponent({
    components: { Button: Button, Select: Select, Option: Option, Input: Input },
    setup: function () {
        var jsonStr = ref('');
        var formDataStr = ref('');
        var value = ref('brackets');
        var options = [
            'indices',
            'brackets',
            'repeat',
            'comma'
        ];
        var handler = function (fn) {
            try {
                fn();
            }
            catch (e) {
                Message.error('出错啦！');
            }
        };
        var format = function () {
            jsonStr.value = prettier.format(jsonStr.value, {
                parser: 'json',
                plugins: [babelParser]
            });
        };
        var trans2formData = function () {
            format();
            formDataStr.value = qs.stringify(JSON.parse(jsonStr.value), {
                arrayFormat: value.value,
                encode: false
            });
        };
        var trans2json = function () {
            jsonStr.value = JSON.stringify(qs.parse(formDataStr.value));
            format();
        };
        return {
            value: value,
            options: options,
            jsonStr: jsonStr,
            formDataStr: formDataStr,
            handler: handler,
            format: format,
            trans2formData: trans2formData,
            trans2json: trans2json
        };
    }
});
